import React, { Component } from 'react';
import { Carousel } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import * as $ from "jquery";

class Slider extends React.Component {

    render() {
        // ----- Slider Start -----//
        let slider = this.props.slider
        let container = []
        let container_mobile = []
        slider.forEach((val, index) => {
            if(val.node.bannerMeta.deviceType.toLowerCase() == "desktop")
            container.push(
                <Carousel.Item key={index}>
                    <div className='offer'>
                        <h4 className='text-uppercase mb-0'>{this.props.offer.offer1}</h4>
                        <hr className='w-75 m-auto my-lg-3 my-2' />
                        <h4 className='text-uppercase mb-0'>{this.props.offer.offer2}</h4>
                        <hr className='m-auto my-lg-3 my-2' />
                        <h4 className='text-uppercase mb-0'>{this.props.offer.offer3}</h4>
                        <hr className='m-auto my-lg-3 my-2' />
                        <h4 className='text-uppercase mb-4' style={{lineHeight : '35px'}}>{this.props.offer.offer4}</h4>
                        <h6 className='text-uppercase mb-3'>{this.props.offer.offer5}</h6>
                        <h6 className='text-uppercase'>{this.props.offer.offer6}</h6>
                        <button type="button" class="btn btn-primary" onClick={() => fnOpenModal()}>ENQUIRY NOW</button>
                    </div>
                    <img
                        className="d-block w-100"
                        src={val.node.bannerMeta.imageUpload.sourceUrl}
                        alt=""
                    />
                    <Carousel.Caption>
                    </Carousel.Caption>
                </Carousel.Item>)
        });
        slider.forEach((val, index) => {
            if(val.node.bannerMeta.deviceType.toLowerCase() == "mobile")
            container_mobile.push(
                <Carousel.Item key={index}>
                    <div className='offer'>
                        <h4 className='text-uppercase mb-0'>{this.props.offer.offer1}</h4>
                        <hr className='w-75 m-auto my-lg-3 my-2' />
                        <h4 className='text-uppercase mb-0'>{this.props.offer.offer2}</h4>
                        <hr className='m-auto my-lg-3 my-2' />
                        <h4 className='text-uppercase mb-0'>{this.props.offer.offer3}</h4>
                        <hr className='m-auto my-lg-3 my-2' />
                        <h4 className='text-uppercase mb-4' style={{lineHeight : '25px'}}>{this.props.offer.offer4}</h4>
                        <h6 className='text-uppercase'>{this.props.offer.offer5}</h6>
                        <button type="button" class="btn btn-primary" onClick={() => fnOpenModal()}>ENQUIRY NOW</button>
                    </div>
                    <img
                        className="d-block w-100"
                        src={val.node.bannerMeta.imageUpload.sourceUrl}
                        alt=""
                    />
                    <Carousel.Caption>
                    </Carousel.Caption>
                </Carousel.Item>)
        });
        // ----- Slider End -----//

        const fnOpenModal = () => {
            $('#enquireModal').addClass("show");
            $('#enquireModal').css("display", "block");
            $('#enquireModal').prop("aria-hidden", "false");
            $("body").append('<div class="modal-backdrop fade show"></div>');
        }

        return (
            <>

                <Carousel className='d-none d-sm-block' fade={true}>
                    {container}
                </Carousel>
                <Carousel className='d-block d-sm-none'>
                    {container_mobile}
                </Carousel>

            </>
        )
    }
}
export default Slider;
