import React, { Component } from 'react';
import { Container, Row, Col, Accordion, Carousel } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import sectionHeaderIcon from '../images/deal.png';


class Location extends React.Component {

    render() {

        // ----- Location Start -----//
        let LocationContentData = this.props.locationContent
        let LocationImageData = this.props.locationImage
        let LocationContent = []
        let LocationImage = []
        LocationContentData.forEach((val, index) => {
            if(index == 0){
                LocationContent.push(
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>{val.node.locationContentMeta.title}</Accordion.Header>
                        <Accordion.Body>
                            <div dangerouslySetInnerHTML={{
                                __html: val.node.locationContentMeta.pointers
                            }}>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                )
            }else{
                LocationContent.push(
                    <Accordion.Item eventKey={index}>
                        <Accordion.Header>{val.node.locationContentMeta.title}</Accordion.Header>
                        <Accordion.Body>
                            <div dangerouslySetInnerHTML={{
                                __html: val.node.locationContentMeta.pointers
                            }}>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                )
            }
        });

        LocationImageData.forEach((image, imageIndex) => {
            LocationImage.push(
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={image.node.locationImagesMeta.image.sourceUrl}
                        alt=""
                    />
                </Carousel.Item>
            )
        });
        // ----- Location End -----//

        return (
            <>

                <Container fluid className="bg-light" id="location">
                    <Row className="section-heading mb-4 mx-md-5 mx-2">
                        <img
                            className="d-block m-auto mb-2"
                            src={sectionHeaderIcon}
                        />
                        <h1 className="m-0">Location Advantages</h1>
                    </Row>
                    <Row className="mx-md-5 mx-4 text-center">
                        <Col sm={12} md={6} lg={6}>
                            <Accordion defaultActiveKey="0" className='mb-md-0 mb-5'>
                                {LocationContent}
                            </Accordion>
                        </Col>
                        <Col sm={12} md={6} lg={6}>
                            {/* <Carousel>
                                {LocationImage}
                            </Carousel> */}
                            <div class="map-container">
                            <div id="LocationMapContent" class="">
                            <iframe
                                src={this.props.contact.mapLink}
                                frameBorder="0"
                                allowFullScreen=""
                                aria-hidden="false"
                                tabIndex="0"
                            />
                            </div>
                        </div>
                        </Col>
                    </Row>
                </Container>

            </>
        )
    }
}
export default Location;