import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Container, Row, Card, Nav, Tab } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import sectionHeaderIcon from '../images/deal.png';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import MultiCarousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

class Gallery extends React.Component {
    constructor() {
        super();

        this.state = {
            photoIndex: 0,
            isOpen: false,
        };

        this.handler = this.handler.bind(this);
    }

    handler(e) {
        this.state({
            photoIndex: e.target.getAttribute("data-index"),
            isOpen: true,
        });
    }

    render() {
        const { photoIndex, isOpen } = this.state;

        // ----- Plans Start -----//
        let GalleryTabData = this.props.galleryTabs
        let GalleryImageData = this.props.galleryImages
        let images = []
        let GalleryTab = []
        let GalleryImage = []
        let GalleryImagePanel = []
        let dataIndx = 0;
        GalleryTabData.forEach((val, index) => {
            GalleryTab.push(
                <Nav.Item key={index}>
                    <Nav.Link eventKey={index}>{val.node.galleryTabMeta.title}</Nav.Link>
                </Nav.Item>
            )
            GalleryImage = []
            let imageCount = 1;
            GalleryImageData.forEach((image, imageIndex) => {
                if (val.node.galleryTabMeta.title == image.node.galleryImageMeta.tabsTitle) {
                    images.push(image.node.galleryImageMeta.image.sourceUrl,)
                    let bg_image = image.node.galleryImageMeta.image.sourceUrl
                    let bg = {
                        backgroundImage : `url("${bg_image}")`
                    }
                    GalleryImage.push(
                        <div className="p-sm-2 p-md-3" key={imageIndex}>
                            <Card className="p-2">
                                    <div className='gallery-images' style={bg} data-index={dataIndx} onClick={(e) => this.setState({ photoIndex: e.target.getAttribute("data-index"), isOpen: true })}>

                                    </div>
                            </Card>
                        </div>
                    )
                    imageCount++;
                    dataIndx++;
                }
            });
            if(imageCount > 3){
                var settings = {
                    dots: false,
                    infinite: true,
                    autoplay: true,
                    speed: 500,
                    slidesToShow: 3,
                    slidesToScroll: 1,
                    initialSlide: 0,
                    responsive: [
                      {
                        breakpoint: 3000,
                        settings: {
                          slidesToShow: 3,
                          slidesToScroll: 1,
                        }
                      },
                      {
                        breakpoint: 1024,
                        settings: {
                          slidesToShow: 2,
                          slidesToScroll: 1,
                        }
                      },
                      {
                        breakpoint: 464,
                        settings: {
                          slidesToShow: 1,
                          slidesToScroll: 1
                        }
                      }
                    ]
                  };
            }else{
                var settings = {
                    dots: false,
                    infinite: false,
                    autoplay: true,
                    speed: 500,
                    slidesToShow: 3,
                    slidesToScroll: 1,
                    initialSlide: 0,
                    responsive: [
                      {
                        breakpoint: 3000,
                        settings: {
                          slidesToShow: 3,
                          slidesToScroll: 1,
                        }
                      },
                      {
                        breakpoint: 1024,
                        settings: {
                          slidesToShow: 2,
                          slidesToScroll: 1,
                        }
                      },
                      {
                        breakpoint: 464,
                        settings: {
                          slidesToShow: 1,
                          slidesToScroll: 1
                        }
                      }
                    ]
                  };
            }
            GalleryImagePanel.push(
                <Tab.Pane eventKey={index}>
                    <Slider {...settings} >
                        {GalleryImage}
                    </Slider>
                </Tab.Pane>
            )
        });
        // ----- Gallery End -----//

        return (
            <>

{isOpen && (
                    <Lightbox
                        mainSrc={images[photoIndex]}
                        nextSrc={images[(photoIndex + 1) % images.length]}
                        prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                        onCloseRequest={() => this.setState({ isOpen: false })}
                        onMovePrevRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + images.length - 1) % images.length,
                            })
                        }
                        onMoveNextRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + 1) % images.length,
                            })
                        }
                    />
                )}

                <Container fluid className="bg-white" id="gallery">
                    <Row className="section-heading mb-4 mx-md-5 mx-2">
                        <img
                            className="d-block m-auto mb-2"
                            src={sectionHeaderIcon}
                        />
                        <h1 className="m-0">Project Gallery</h1>
                    </Row>
                    <Row className="mx-md-5 mx-4 text-center">
                        <Tab.Container defaultActiveKey="0">
                            <Nav variant="pills">
                                {GalleryTab}
                            </Nav>
                            <Tab.Content className="pt-5">
                                {GalleryImagePanel}
                            </Tab.Content>
                        </Tab.Container>
                    </Row>
                </Container>

            </>
        )
    }
}
export default Gallery;
