import React, { Component } from 'react';
import { Container, Row, Card, Col } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import * as $ from "jquery";
import sectionHeaderIcon from '../images/deal.png';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import MultiCarousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

const responsive = {
    superLargeDesktop: {
        // the naming can be any, depends on you.
        breakpoint: { max: 4000, min: 3000 },
        items: 5
    },
    desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 3
    },
    tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 1
    }
};

class Price extends React.Component {

    render() {

        // ----- Price Start -----//
        let PriceData = this.props.price
        let Price = []
        let imageCount = 1;
        PriceData.forEach((val, index) => {
            Price.push(
                <div className="p-sm-2 p-md-3" key={index}>
                    <Card className="pricing-card">
                        <Card.Body>
                            <h3 className="pricing-plan-title">{val.node.priceContentMeta.title}</h3>
                            <h6>{val.node.priceContentMeta.area}</h6>
                        </Card.Body>
                        <Card.Footer>
                            <a href="javascript:void(0);" onClick={() => fnOpenModal()} className="card-link pricing-plan-purchase-btn">Get Price</a>
                        </Card.Footer>
                    </Card>
                </div>
            )
            imageCount++;
        });
        if (imageCount > 3) {
            var settings = {
                dots: false,
                infinite: true,
                autoplay: true,
                speed: 500,
                slidesToShow: 3,
                slidesToScroll: 1,
                initialSlide: 0,
                responsive: [
                    {
                        breakpoint: 3000,
                        settings: {
                            slidesToShow: 3,
                            slidesToScroll: 1,
                        }
                    },
                    {
                        breakpoint: 1024,
                        settings: {
                            slidesToShow: 2,
                            slidesToScroll: 1,
                        }
                    },
                    {
                        breakpoint: 464,
                        settings: {
                            slidesToShow: 1,
                            slidesToScroll: 1
                        }
                    }
                ]
            };
        } else {
            var settings = {
                dots: false,
                infinite: false,
                autoplay: true,
                speed: 500,
                slidesToShow: 3,
                slidesToScroll: 1,
                initialSlide: 0,
                responsive: [
                    {
                        breakpoint: 3000,
                        settings: {
                            slidesToShow: 3,
                            slidesToScroll: 1,
                        }
                    },
                    {
                        breakpoint: 1024,
                        settings: {
                            slidesToShow: 2,
                            slidesToScroll: 1,
                        }
                    },
                    {
                        breakpoint: 464,
                        settings: {
                            slidesToShow: 1,
                            slidesToScroll: 1
                        }
                    }
                ]
            };
        }
        // ----- Price End -----//

        const fnOpenModal = () => {
            $('#enquireModal').addClass("show");
            $('#enquireModal').css("display", "block");
            $('#enquireModal').prop("aria-hidden", "false");
            $("body").append('<div class="modal-backdrop fade show"></div>');
        }

        return (
            <>

                <Container fluid className="bg-light" id="price">
                    <Row className="section-heading mb-4 mx-md-5 mx-2">
                        <img
                            className="d-block m-auto mb-2"
                            src={sectionHeaderIcon}
                        />
                        <h1 className="m-0">Project Price</h1>
                    </Row>
                    <Row className="mx-md-5 mx-4 text-center">
                        <Col>
                            <Slider {...settings} >
                                {Price}
                            </Slider>
                        </Col>
                    </Row>
                </Container>

            </>
        )
    }
}
export default Price;
