import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Container, Row, Card, Nav, Tab } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import sectionHeaderIcon from '../images/deal.png';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

class Plans extends React.Component {
    constructor() {
        super();

        this.state = {
            photoIndex: 0,
            isOpen: false,
        };

        this.handler = this.handler.bind(this);
    }

    handler(e) {
        this.state({
            photoIndex: e.target.getAttribute("data-index"),
            isOpen: true,
        });
    }

    render() {
        const { photoIndex, isOpen } = this.state;

        // ----- Plans Start -----//
        let PlansTabData = this.props.planTabs
        let PlansImageData = this.props.planImages
        let PlansImagePanel = []
        let images = []
        let PlansTab = []
        let PlansImage = []
        let dataIndx = 0;

        PlansTabData.forEach((val, index) => {
            PlansTab.push(
                // <li className='custom-tab-item'><a className={`custom-tab-link ${index == 0 ? 'active' : ''} `} href="javascript:void(0);" data-index={index} onClick={(e) => fnTabsChange(e.target.getAttribute("data-index"))}>{val.node.projectTabMeta.title}</a></li>
                <Nav.Item key={index}>
                    <Nav.Link data-index={index} eventKey={index}>{val.node.projectTabMeta.title}</Nav.Link>
                </Nav.Item>
            )
            PlansImage = []
            let imageCount = 1;
            PlansImageData.forEach((image, imageIndex) => {
                if (val.node.projectTabMeta.title.trim() == image.node.projectImagesMeta.tabTitle.trim()) {
                    images.push(image.node.projectImagesMeta.image.sourceUrl,)
                    let bg_image = image.node.projectImagesMeta.image.sourceUrl
                    let bg = {
                        backgroundImage : `url("${bg_image}")`
                    }
                    PlansImage.push(
                        <div className="p-sm-2 p-md-3" key={imageIndex}>
                            <Card className="item p-2">
                            <div className='gallery-images' style={bg} data-index={dataIndx} onClick={(e) => this.setState({ photoIndex: e.target.getAttribute("data-index"), isOpen: true })}>

                            </div>
                                {/* <Card.Img src={image.node.projectImagesMeta.image.sourceUrl} data-index={dataIndx} onClick={(e) => this.setState({ photoIndex: e.target.getAttribute("data-index"), isOpen: true })} /> */}
                            </Card>
                        </div>
                    )
                    imageCount++;
                    dataIndx++;
                }
            });
            if(imageCount > 3){
                var settings = {
                    dots: false,
                    infinite: true,
                    autoplay: true,
                    speed: 500,
                    slidesToShow: 3,
                    slidesToScroll: 1,
                    initialSlide: 0,
                    responsive: [
                      {
                        breakpoint: 3000,
                        settings: {
                          slidesToShow: 3,
                          slidesToScroll: 1,
                        }
                      },
                      {
                        breakpoint: 1024,
                        settings: {
                          slidesToShow: 2,
                          slidesToScroll: 1,
                        }
                      },
                      {
                        breakpoint: 464,
                        settings: {
                          slidesToShow: 1,
                          slidesToScroll: 1
                        }
                      }
                    ]
                  };
            }else{
                var settings = {
                    dots: false,
                    infinite: false,
                    autoplay: true,
                    speed: 500,
                    slidesToShow: 3,
                    slidesToScroll: 1,
                    initialSlide: 0,
                    responsive: [
                      {
                        breakpoint: 3000,
                        settings: {
                          slidesToShow: 3,
                          slidesToScroll: 1,
                        }
                      },
                      {
                        breakpoint: 1024,
                        settings: {
                          slidesToShow: 2,
                          slidesToScroll: 1,
                        }
                      },
                      {
                        breakpoint: 464,
                        settings: {
                          slidesToShow: 1,
                          slidesToScroll: 1
                        }
                      }
                    ]
                  };
            }
            PlansImagePanel.push(
                <Tab.Pane eventKey={index}>
                    <Slider {...settings} >
                        {PlansImage}
                    </Slider>
                </Tab.Pane>
            )
        });
        // ----- Plans End -----//

        return (
            <>

                {isOpen && (
                    <Lightbox
                        mainSrc={images[photoIndex]}
                        nextSrc={images[(photoIndex + 1) % images.length]}
                        prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                        onCloseRequest={() => this.setState({ isOpen: false })}
                        onMovePrevRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + images.length - 1) % images.length,
                            })
                        }
                        onMoveNextRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + 1) % images.length,
                            })
                        }
                    />
                )}

                <Container fluid className="bg-white" id="plans">
                    <Row className="section-heading mb-4 mx-md-5 mx-2">
                        <img
                            className="d-block m-auto mb-2"
                            src={sectionHeaderIcon}
                        />
                        <h1 className="m-0">Project Plans</h1>
                    </Row>
                    <Row className="mx-md-5 mx-4 text-center">
                        <Tab.Container defaultActiveKey="0">
                            <Nav variant="pills">
                                {PlansTab}
                            </Nav>
                            <Tab.Content className="pt-5">
                                {PlansImagePanel}
                            </Tab.Content>
                        </Tab.Container>
                    </Row>
                </Container>

            </>
        )
    }
}
export default Plans;
